import React, { Component } from 'react'

import facebook from '../media/socials/facebook.png';
import instagram from '../media/socials/instagram.png';
import pinterest from '../media/socials/pinterest.png';
import ExitToAppIcon from '@material-ui/icons/ExitToApp';

const DelayLink = React.lazy(() => import('./DelayLink'));
const axios = require('axios');

export class Nav extends Component {

    render() {
        return (
            <nav className={`${this.props.nav === null ? 'gone' : (this.props.nav ? 'animate__animated animate__slideInRight' : 'animate__animated animate__slideOutRight')}`}>
                <ul>
                    <li>
                        <DelayLink same={this.props.toggleNav} delay={600} to="/" onDelayStart={this.props.runLoader}>Home</DelayLink>
                    </li>
                    <li className="logIn">
                        {!this.props.loggedIn ? 
                        <DelayLink same={this.props.toggleNav} delay={600} to="/login" onDelayStart={this.props.runLoader}>Log In</DelayLink>
                        :
                        <DelayLink same={this.props.toggleNav} delay={600} to="/dashboard" onDelayStart={this.props.runLoader}>Dashboard</DelayLink>}
                    </li>
                    <li>
                        <DelayLink same={this.props.toggleNav} delay={600} to="/portfolio" onDelayStart={this.props.runLoader}>Portfolio</DelayLink>
                    </li>
                    <li>
                        <DelayLink same={this.props.toggleNav} delay={600} to="/bookings" onDelayStart={this.props.runLoader}>Bookings</DelayLink>
                    </li>
                    <li>
                        <DelayLink same={this.props.toggleNav} delay={600} to="/packages" onDelayStart={this.props.runLoader}>Packages</DelayLink>
                    </li>
                    <li>
                        <DelayLink same={this.props.toggleNav} delay={600} to="/shop" onDelayStart={this.props.runLoader}>Shop</DelayLink>
                    </li>
                    <li>
                        <DelayLink same={this.props.toggleNav} delay={600} to="/about" onDelayStart={this.props.runLoader}>About</DelayLink>
                    </li>
                    <li>
                        <DelayLink same={this.props.toggleNav} delay={600} to="/contact" onDelayStart={this.props.runLoader}>Contact</DelayLink>
                    </li>
                </ul>
                <div className="footer">
                    {this.props.loggedIn && <p style={{marginTop: 0, cursor: 'pointer'}} className="logout" onClick={() => {axios.get('/logout'); this.props.checkLoggedIn(); window.location.href = 'https://thomasyarnell.com/login'}}>Logout</p>}
                    <div className="socials">
                        <a href="https://www.facebook.com/thomas.yarnell.52" target="_blank" rel="noreferrer"><img src={facebook} alt="facebook"/></a>
                        <a href="https://www.instagram.com/thomasyarnellphotography/" target="_blank" rel="noreferrer"><img src={instagram} alt="instagram"/></a>
                        <a href="https://www.pinterest.com/thomasyarnellphotos/" target="_blank" rel="noreferrer"><img src={pinterest} alt="pinterest"/></a>
                    </div>
                    <p><u><a href="https://docs.google.com/document/d/1RQrDCJCd-vfh53CCAZD2gdHYGdDp6tclo9NcIjGPrYE/edit?usp=sharing" target="_blank" rel="noreferrer">Citations</a></u></p>
                </div>
            </nav>
        )
    }
}

export default Nav
